import { Link } from 'react-router-dom';
import './NotFound.css'
const NotFound = () => {
    return ( 
        <div className="not-found">
            <h2>Sorry</h2>
            <p>That page cannot be found</p>
            <Link to="/home">Back to the homepage</Link>
        </div>
     );
}
 
export default NotFound;